@tailwind components;
@tailwind base;

@layer components {
	.nfd-root {
		.nfd-alert {
			@apply
			nfd-flex
			nfd-gap-3
			nfd-rounded-md
			nfd-p-4;
		}

		/* Variants */
		.nfd-alert--info {
			@apply nfd-bg-blue-100;

			.nfd-alert__message {
				@apply nfd-text-blue-800;
			}
		}

		.nfd-alert--warning {
			@apply nfd-bg-amber-100;

			.nfd-alert__message {
				@apply nfd-text-amber-800;
			}
		}

		.nfd-alert--success {
			@apply nfd-bg-green-100;

			.nfd-alert__message {
				@apply nfd-text-green-800;
			}
		}

		.nfd-alert--error {
			@apply nfd-bg-red-100;

			.nfd-alert__message {
				@apply nfd-text-red-800;
			}
		}

		/* Elements */
		.nfd-alert__icon {
			@apply
			nfd-shrink-0
			nfd-grow-0
			nfd-h-5
			nfd-w-5;
		}
	}
}
@layer components {
	.nfd-root {
		.nfd-autocomplete {
			@apply nfd-relative;
		}

		.nfd-autocomplete--error {
			.nfd-autocomplete__button {
				@apply
				nfd-border-red-300

				focus:nfd-outline-none
				focus:nfd-ring-red-500
				focus:nfd-border-red-500;
			}

			.nfd-autocomplete__input {
				@apply nfd-placeholder-red-300;
			}
		}

		.nfd-autocomplete--disabled {

			@apply
			nfd-opacity-50
			nfd-cursor-not-allowed
			focus:nfd-ring-0;
		}

		.nfd-autocomplete__button {
			@apply
			nfd-w-full
			nfd-h-full
			nfd-rounded-md
			nfd-shadow-sm
			nfd-border
			nfd-border-slate-300
			nfd-flex
			nfd-items-center
			nfd-px-3
			focus-within:nfd-border-primary-500
			focus-within:nfd-outline-none
			focus-within:nfd-ring-1
			focus-within:nfd-ring-primary-500;
		}

		.nfd-autocomplete__button-icon {
			@apply nfd-absolute nfd-top-[0.6875rem] nfd-right-2.5 nfd-text-slate-400 nfd-pointer-events-none nfd-w-5 nfd-h-5;
		}

		.nfd-autocomplete__input {
			@apply
			nfd-w-full
			nfd-border-0
			nfd-bg-white
			nfd-py-2
			nfd-pl-0
			nfd-pr-10
			nfd-shadow-none
			nfd-text-sm
			focus:nfd-ring-0
			nfd-text-slate-800;
		}

		.nfd-autocomplete__options {
			@apply
			nfd-absolute
			nfd-z-20
			nfd-w-full
			nfd-mt-1
			nfd-overflow-auto
			nfd-bg-white
			nfd-rounded-md
			nfd-shadow-lg
			nfd-max-h-60
			nfd-ring-1
			nfd-ring-black
			nfd-ring-opacity-5
			focus:nfd-outline-none
			nfd-text-sm;
		}

		.nfd-autocomplete__option {
			@apply
			nfd-cursor-default
			nfd-select-none
			nfd-relative
			nfd-flex
			nfd-items-center
			nfd-justify-between
			nfd-px-3
			nfd-py-2
			nfd-text-slate-700;

			&--active {
				@apply nfd-bg-slate-200;
			}

			&--selected {
				@apply nfd-bg-primary-500 nfd-text-white;
			}
		}

		.nfd-select__option-label {
			@apply nfd-block nfd-truncate;
		}

		.nfd-autocomplete__option-check {
			@apply
			nfd-w-5
			nfd-h-5
			nfd-text-white
			nfd-shrink-0;
		}
	}
}
@layer components {
	.nfd-root {
		.nfd-badge {
			@apply
			nfd-inline-flex
			nfd-items-center
			nfd-px-2
			nfd-py-0.5
			nfd-rounded-full
			nfd-text-xs
			nfd-font-medium
			nfd-align-middle
			nfd-leading-tight
			nfd-whitespace-nowrap
			nfd-bg-blue-100
			nfd-text-blue-800;
		}

		/* Variants */

		.nfd-badge--info {
			@apply
			nfd-bg-blue-200
			nfd-text-blue-900;
		}

		.nfd-badge--upsell {
			@apply
			nfd-bg-amber-200
			nfd-text-amber-900;
		}

		.nfd-badge--plain {
			@apply
			nfd-bg-slate-200
			nfd-text-slate-900;
		}

		/* Sizes */

		.nfd-badge--small {
			@apply nfd-text-xxs;
		}

		.nfd-badge--large {
			@apply
			nfd-px-3
			nfd-text-base;
		}
	}
}
@layer components {
	.nfd-root {
		.nfd-button {
			@apply
			nfd-inline-flex
			nfd-items-center
			nfd-justify-center
			nfd-py-2
			nfd-px-3
			nfd-border
			nfd-shadow-sm
			nfd-no-underline
			nfd-cursor-pointer
			nfd-rounded-md
			nfd-text-sm
			nfd-font-medium
			nfd-leading-4
			nfd-text-center

			focus:nfd-outline-none
			focus:nfd-ring-2
			focus:nfd-ring-offset-2
			focus:nfd-ring-primary-500;
		}

		/* Special styling for buttons rendered `as` "a" (to override anchor defaults). */
		a.nfd-button {
			@apply
			focus:nfd-ring-offset-white
			focus:nfd-rounded-md;
		}

		/* Variants */

		.nfd-button--primary {
			@apply
			nfd-text-white
			nfd-bg-primary-500
			nfd-border-transparent

			hover:nfd-text-white
			hover:nfd-bg-primary-700
			focus:nfd-text-white
			focus:nfd-ring-primary-700
			visited:nfd-text-white
			visited:hover:nfd-text-white;
		}

		.nfd-button--secondary {
			@apply
			nfd-text-slate-800
			nfd-bg-white
			nfd-border-slate-300

			hover:nfd-text-slate-800
			hover:nfd-bg-slate-50
			focus:nfd-text-slate-800
			focus:nfd-ring-primary-700
			visited:nfd-text-slate-800
			visited:hover:nfd-text-slate-800;
		}

		.nfd-button--error {
			@apply
			nfd-text-white
			nfd-bg-red-600
			nfd-border-transparent

			hover:nfd-text-white
			hover:nfd-bg-red-700
			focus:nfd-text-white
			focus:nfd-ring-red-600
			visited:nfd-text-white
			visited:hover:nfd-text-white;
		}

		.nfd-button--upsell {
			@apply
			nfd-text-amber-900
			nfd-bg-amber-300
			nfd-border-transparent

			hover:nfd-text-amber-900
			hover:nfd-bg-amber-400
			focus:nfd-text-amber-900
			focus:nfd-ring-amber-400
			visited:nfd-text-amber-900
			visited:hover:nfd-text-amber-900;
		}

		/* Sizes */

		.nfd-button--large {
			@apply
			nfd-py-3
			nfd-px-6
			nfd-text-base
			nfd-leading-5;
		}

		.nfd-button--small {
			@apply
			nfd-text-xs
			nfd-py-1.5
			nfd-px-2.5;
		}

		/* States */

		.nfd-button--disabled {
			@apply
			nfd-opacity-50
			nfd-cursor-not-allowed

			focus:nfd-ring-0;
		}
	}
}
@layer components {
	.nfd-root {
		.nfd-checkbox {
			@apply
			nfd-flex
			nfd-items-center;
		}

		.nfd-checkbox--disabled {
			.nfd-checkbox__input,
			.nfd-checkbox__label {
				@apply nfd-opacity-50 nfd-cursor-not-allowed;
			}
		}

		.nfd-checkbox__input {
			@apply
			nfd-h-4
			nfd-w-4
			nfd-text-primary-500
			nfd-border-slate-300
			nfd-rounded
			nfd-shadow-none
			nfd-cursor-default

			focus:nfd-border-slate-300
			focus:nfd-ring-primary-500;

			&:checked {
				@apply
				before:nfd-content-none
				before:nfd-hidden;
			}
		}

		.nfd-checkbox__label {
			@apply nfd-ml-3;
		}
	}
}
@layer components {
	.nfd-root {
		.nfd-code {
			@apply
			nfd-inline-block
			nfd-text-xs
			nfd-leading-tight
			nfd-text-slate-900
			nfd-bg-slate-200
			nfd-p-1
			nfd-m-0
			nfd-rounded;
		}

		.nfd-code--block {
			@apply
			nfd-block
			nfd-my-2
			nfd-px-2
			nfd-py-1
			nfd-whitespace-nowrap
			nfd-max-w-full
			nfd-overflow-x-auto;
		}
	}
}
@layer components {
	.nfd-root {
		.nfd-drop-zone {
			@apply
			nfd-w-full
			nfd-px-6
			nfd-pt-5
			nfd-pb-6
			nfd-border-2
			nfd-border-dashed
			nfd-rounded-md
			nfd-transition-all
			nfd-ease-out
			nfd-duration-300
			nfd-bg-white
			nfd-border-slate-300;

			&.nfd-drop-zone-is-dragging-over {
				@apply nfd-bg-primary-50 nfd-border-primary-300;

				.nfd-drop-zone__content {
					@apply nfd-pointer-events-none;
				}
			}

			&.nfd-drop-zone-is-disabled {
				@apply nfd-opacity-50 nfd-cursor-not-allowed;
			}
		}
	}
}@layer components {
    .nfd-root {
        .nfd-file-input {
			@apply
			nfd-w-full
			nfd-px-6
			nfd-pt-5
			nfd-pb-6
			nfd-border-2
			nfd-border-dashed
			nfd-rounded-md
			nfd-text-center
			nfd-transition-all
			nfd-ease-out
			nfd-duration-300
			nfd-bg-white
			nfd-border-slate-300;

			&.nfd-is-drag-over {
				@apply nfd-bg-primary-50 nfd-border-primary-300;

				.nfd-file-input__content {
					@apply nfd-pointer-events-none;
				}

				.nfd-file-input__icon {
					@apply -nfd-translate-y-2 nfd-text-primary-400;
				}
			}

			&.nfd-is-disabled {
				@apply nfd-opacity-50 nfd-cursor-not-allowed;

				.nfd-file-input__select-label {
					@apply nfd-cursor-not-allowed;
				}
			}
		}

		.nfd-file-input__content {
			@apply
			nfd-max-w-xs
			nfd-inline-flex
			nfd-flex-col
			nfd-space-y-1
			nfd-items-center
			nfd-text-center;
		}

		.nfd-file-input__icon {
			@apply
			nfd-mx-auto
			nfd-h-12
			nfd-w-12
			nfd-text-slate-400
			nfd-stroke-1
			nfd-transition-all
			nfd-ease-out
			nfd-duration-300
			nfd-transform;

			> path {
				stroke-width: 1;
			}
		}

		.nfd-file-input__input {
			@apply nfd-sr-only;

			&:focus + .nfd-file-input__select-label {
				@apply nfd-outline-none nfd-ring-2 nfd-ring-offset-2 nfd-ring-primary-500;
			}
		}

		.nfd-file-input__labels {
			@apply nfd-flex nfd-font-normal nfd-text-slate-800;
		}

		.nfd-file-input__select-label {
			@apply nfd-rounded-md nfd-font-medium;
		}
    }

	[dir=rtl] .nfd-root .nfd-file-input__labels {
		@apply nfd-flex-row-reverse;
	}
}@layer components {
	.nfd-root {
		.nfd-image-input {
			@apply
			nfd-flex
			nfd-items-center
			nfd-gap-4
			nfd-min-w-max;

			.nfd-image-input__preview {
				@apply
				nfd-w-12
				nfd-h-12
				nfd-relative
				nfd-overflow-hidden
				nfd-flex
				nfd-items-center
				nfd-justify-center;

				&:focus-within {
					@apply nfd-outline-none nfd-ring-2 nfd-ring-offset-2 nfd-ring-primary-500;
				}
			}

			.nfd-image-input__preview-img {
				@apply
				nfd-w-full
				nfd-h-full
				nfd-object-cover
				nfd-z-10;
			}

			.nfd-image-input__preview-reset {
				@apply
				nfd-absolute
				nfd-inset-0
				nfd-p-2
				nfd-opacity-0
				nfd-bg-slate-900/60
				nfd-text-white
				nfd-transition-[opacity]
				nfd-ease-out
				nfd-duration-100
				nfd-delay-100
				nfd-z-20
				nfd-cursor-pointer;

				&:focus {
					@apply nfd-opacity-100;
				}
			}

			.nfd-image-input__icon {
				@apply
				nfd-text-slate-400
				nfd-w-12
				nfd-h-12;
			}

			&.nfd-has-preview {
				.nfd-image-input__preview {
					@apply nfd-border nfd-border-slate-200;
				}
			}

			&.nfd-is-disabled {
				.nfd-image-input__preview {
					@apply nfd-opacity-50 nfd-cursor-not-allowed;
				}
				

				.nfd-image-input__preview-reset {
					@apply nfd-hidden nfd-cursor-not-allowed;
				}

				.nfd-image-input__icon {
					@apply nfd-opacity-50 nfd-cursor-not-allowed;
				}
			}
		}
	}
}@layer components {
	.nfd-root {
		.nfd-label {
			@apply
			nfd-text-sm
			nfd-font-medium
			nfd-text-slate-800;
		}

		.nfd-label__required {
			@apply
			nfd-ml-0.5
			nfd-text-red-500;
		}
	}
}
@layer components {
	.nfd-root {
		.nfd-link {
			@apply
			nfd-text-indigo-600
			nfd-cursor-pointer
			nfd-underline

			hover:nfd-text-indigo-500
			focus:nfd-text-indigo-500
			focus:nfd-outline-none
			focus:nfd-ring-1
			focus:nfd-ring-offset-1
			focus:nfd-ring-offset-transparent
			focus:nfd-ring-indigo-600
			focus:nfd-rounded-sm
			visited:nfd-text-primary-500
			visited:hover:nfd-text-primary-400;
		}

		/* Variants */
		.nfd-link--default {
		}

		.nfd-link--primary {
			@apply nfd-text-primary-600 hover:nfd-text-primary-500 focus:nfd-text-primary-500 focus:nfd-ring-primary-600;
		}

		.nfd-link--error {
			@apply nfd-text-red-600 hover:nfd-text-red-500 focus:nfd-text-red-500 focus:nfd-ring-red-600;
		}
	}
}
@layer components {
	.nfd-root {
		.nfd-paper {
			@apply nfd-flex nfd-flex-col nfd-rounded-lg nfd-bg-white nfd-shadow;
		}

		.nfd-paper__header {
			@apply nfd-p-8 nfd-border-b;
		}

		.nfd-paper__content {
			@apply nfd-flex-grow nfd-p-8;
		}
	}
}
@layer components {
	.nfd-root {
		.nfd-progress-bar {
			@apply nfd-block nfd-w-full nfd-bg-slate-200 nfd-rounded-full nfd-overflow-hidden;
		}

		.nfd-progress-bar__progress {
			@apply
			nfd-block
			nfd-h-1.5
			nfd-bg-primary-500
			nfd-rounded-full
			nfd-transform
			nfd-transition-all
			nfd-ease-linear
			nfd-duration-200;
		}
	}
}
@layer components {
	.nfd-root {
		.nfd-radio {
			@apply nfd-flex nfd-items-center;
		}

		/* Variants */
		.nfd-radio--disabled {
			.nfd-radio__input,
			.nfd-radio__label,
			.nfd-radio__check {
				@apply
				nfd-opacity-50
				nfd-cursor-not-allowed
				focus:nfd-ring-0;
			}
		}

		.nfd-radio--inline-block {
			@apply nfd-inline-flex;

			/* Elements of the inline-block variant */

			.nfd-radio__input {
				@apply nfd-sr-only;
				&:checked + .nfd-radio__content {
					.nfd-radio__label {
						@apply nfd-border-transparent nfd-ring-2 nfd-ring-primary-500;
					}

					.nfd-radio__check {
						@apply nfd-visible;
					}
					
				}

				&:focus + .nfd-radio__content {
					.nfd-radio__label {
						@apply nfd-ring-2 nfd-ring-primary-500 nfd-ring-offset-2;
					}
				}

				&:checked {
					&:focus + .nfd-radio__content {
						.nfd-radio__label {
							@apply nfd-ring-offset-1 nfd-ring-[3px];
						}
					}
				} 
			}

			.nfd-radio__content {
				@apply nfd-relative;
			}

			.nfd-radio__label {
				@apply
				nfd-flex
				nfd-h-14
				nfd-w-14
				nfd-ml-0
				nfd-justify-center
				nfd-items-center
				nfd-text-base
				nfd-bg-white
				nfd-border
				nfd-rounded-lg
				nfd-shadow-sm
				nfd-cursor-pointer
				nfd-border-slate-300
				focus:nfd-ring-2 focus:nfd-ring-primary-500 focus:nfd-ring-offset-2
				hover:nfd-border-slate-400
				focus:nfd-outline-none;
			}

			.nfd-radio__check {
				@apply
				nfd-invisible
				nfd-absolute
				nfd-top-0.5
				nfd-right-0.5
				nfd-h-5
				nfd-w-5
				nfd-text-primary-600;
			}
		}

		/* State */

		.nfd-radio__input {
			@apply
			nfd-h-4
			nfd-w-4
			nfd-text-primary-500
			nfd-border-slate-300

			focus:nfd-ring-primary-500;
		}

		.nfd-radio__label {
			@apply nfd-ml-3;
		}
	}
}
@layer components {
	.nfd-root {
		.nfd-select {
			@apply nfd-relative;
		}

		.nfd-select--disabled {
			.nfd-select__label,
			.nfd-select__button {
				@apply nfd-opacity-50 nfd-cursor-not-allowed;
			}
		}

		.nfd-select__button {
			@apply
			nfd-relative
			nfd-w-full
			nfd-leading-6
			nfd-py-2
			nfd-px-3
			nfd-text-left
			nfd-text-slate-800
			nfd-bg-white
			nfd-border
			nfd-border-slate-300
			nfd-rounded-md
			nfd-shadow-sm
			nfd-cursor-default
			nfd-flex
			nfd-items-center
			nfd-justify-between

			focus:nfd-outline-none
			focus:nfd-ring-1
			focus:nfd-ring-primary-500
			focus:nfd-border-primary-500;
		}

		.nfd-select__button-icon {
			@apply nfd-absolute nfd-top-2.5 nfd-right-2.5 nfd-text-slate-400 nfd-pointer-events-none nfd-w-5 nfd-h-5;
		}

		.nfd-select__options {
			@apply
			nfd-absolute
			nfd-z-10
			nfd-w-full
			nfd-mt-1
			nfd-overflow-auto
			nfd-bg-white
			nfd-rounded-md
			nfd-shadow-lg
			nfd-max-h-60
			nfd-ring-1
			nfd-ring-black
			nfd-ring-opacity-5
			focus:nfd-outline-none
			nfd-text-sm;
		}

		.nfd-select__option {
			@apply
			nfd-cursor-default
			nfd-select-none
			nfd-relative
			nfd-flex
			nfd-items-center
			nfd-justify-between
			nfd-px-3
			nfd-py-2
			nfd-text-slate-700;

			&--active {
				@apply nfd-bg-slate-200;
			}

			&--selected {
				@apply nfd-text-white nfd-bg-primary-600;
			}
		}

		.nfd-select__option-label,
		.nfd-select__button-label {
			@apply nfd-block nfd-truncate;
		}

		.nfd-select__option-check {
			@apply
			nfd-w-5
			nfd-h-5
			nfd-text-white
			nfd-shrink-0;
		}
	}
}
@layer components {
	.nfd-root {
		.nfd-tag-input {
			@apply
			nfd-flex
			nfd-flex-wrap
			nfd-items-center
			nfd-py-2
			nfd-px-3
			nfd-gap-1.5
			nfd-border
			nfd-border-slate-300
			nfd-rounded-md
			nfd-shadow-sm
